.auth__main{
    display: flex;
    background-color: $primary;
    align-items: center;
    justify-content: center;
    margin: 0;
    height: 100vh;
    width: 100vw;
}
.auth__box-container{
    background-color: white;
    box-shadow:0px 3px $dark-primary;
    border-radius: 2px;
    padding: 20px;
    width: 250px;
}

.auth__title{
    color: $primary;
    text-align: center;
    margin-bottom: 20px;
}
.auth__input {
    color:darken($color: gray, $amount: 60);;
    border: 0px;
    border-bottom: 1px solid $light-primary;
    font-size: 16px;
    margin-bottom: 10px;
    height: 20px;
    width: 100%;

    transition: border-bottom .3s ease;
    
    &:focus {
        border-bottom: 1px solid $primary;
        outline: none;
    }
}

.auth__social-networks {
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
}


.auth__alert-error {
    background-color: red;
    border-radius: 5px;
    color: white;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    padding: 5px
}